import * as React from "react"
import { Component, } from "react"

export class ErrorBoundary extends Component {
  __init() {this.state = {
    error: undefined ,
  }}

  constructor(props) {
    super(props);ErrorBoundary.prototype.__init.call(this);
  }

   static getDerivedStateFromError(error) {
    // Update state so the next render will show the fallback UI.
    return { error }
  }
  componentDidCatch(error, errorInfo) {
    console.error(error, errorInfo)
  }

   static formatShader(
    message,
    shaderSource,
  ) {
    const errors = [...message.matchAll(/ERROR: \d+:(\d+): (.*)/g)].map(
      ([_, line, message]) => ({ line: +line, message }),
    )
    return (
      React.createElement('pre', { style: { fontSize: "small" },}
        , shaderSource.split(/\r\n|\r|\n/).flatMap((line, lineIndex) => {
          const lineNo = lineIndex + 1
          const lineError = errors.find((e) => e.line === lineNo)
          return [
            React.createElement('div', {
              key: lineNo,
              style: 
                lineError
                  ? {
                      backgroundColor: "red",
                      color: "white",
                    }
                  : {}
              ,}

              , ("" + lineNo).padStart(3, " "), " " , line
            ),
            lineError && (
              React.createElement('div', {
                style: {
                  color: "red",
                  fontWeight: "bolder",
                },}

                , "    " + lineError.message
              )
            ),
          ].filter(Boolean)
        })
      )
    )
  }

  render() {
    const error = this.state.error
    if (error) {
      return (
        React.createElement('div', {
          style: {
            padding: 16,
            border: "3px solid orange",
            margin: "16px 200px",
          },}

          , React.createElement('pre', { style: { color: "red", fontWeight: "bolder" },}
            , error.message
          )
          , error.shaderSource &&
            ErrorBoundary.formatShader(error.message, error.shaderSource)
          , React.createElement('pre', { style: { fontSize: "smaller" },}, error.stack)
        )
      )
    }
    return this.props.children
  }
}
